.cali-component.cali-media video {
  width: 0%;
  height: 0%;
}
.cali-component.cali-media .cali-subtitles {
  width: 100%;
}
.cali-component.cali-media .cali-subtitles.empty {
  display: none;
}
.cali-component.cali-media.cali-media-hidden {
  display: none;
}
.cali-component.cali-media.cali-media-showing {
  display: block;
}
.cali-component.cali-media.cali-video {
  width: 100%;
  height: 100%;
}
.cali-component.cali-media.cali-video video {
  width: 100%;
  height: 100%;
}
.cali-component.cali-media.cali-audio {
  width: 0 !important;
  height: 0 !important;
}
.cali-component.cali-media.cali-audio video {
  width: 0 !important;
  height: 0 !important;
}
