.cali-dropdown ul {
  position: absolute;
  top: 0px;
  left: 100%;
  height: 0px;
  list-style: none;
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}
.cali-dropdown li {
  position: relative;
  box-sizing: border-box;
  overflow: hidden;
}
.cali-dropdown .cali-has-sub-menu:hover {
  overflow: visible;
}
.cali-dropdown .cali-has-sub-menu:hover > ul {
  height: auto;
  opacity: 1;
}
.cali-dropdown button {
  text-align: left;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.cali-dropdown > ul {
  position: relative;
  top: 0px;
  left: 0px;
  width: 100%;
  height: auto;
  table-layout: fixed;
  white-space: nowrap;
  opacity: 1;
}
.cali-dropdown > ul > li {
  display: inline-block;
  height: auto;
  vertical-align: top;
}
.cali-dropdown > ul > li > ul {
  position: absolute;
  top: 100%;
  left: 0px;
  z-index: 99;
}
