.cali-preloader {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background-color: #fff;
  opacity: 1;
  transition: opacity 0.3s ease-in-out 0s, height 0s linear 0s;
}
.cali-preloader.removed {
  opacity: 0;
  height: 0%;
  transition: opacity 0.3s ease-in-out 0.2s, height 0s linear 0.3s;
}
